import { Component, ChangeDetectionStrategy, Inject, Optional, OnInit, OnDestroy } from '@angular/core';
import { FormBuilder } from '@angular/forms';
import { PUBSUB, Pubsub } from '@backbase/foundation-ang/web-sdk';
import { TransactionSigningHelperService } from '@backbase/identity-auth/transaction-signing';
import {
  StateService,
  NavigationService,
  RoutableModalService,
  PaymentOrderHttpService,
  BB_PAYORD_PAYMENT_ERROR_MESSAGES_TOKEN,
  PayordPaymentError,
} from '@backbase/payment-orders-ang';
import {
  CreatePaymentConfigSetupService,
  CreatePaymentService,
  ReviewPaymentContainerComponent,
} from '@backbase/payments-collection-ui-ang';

@Component({
  selector: 'bb-custom-review-screen',
  templateUrl: './custom-review-screen.component.html',
  changeDetection: ChangeDetectionStrategy.OnPush,
})
export class CustomReviewScreenComponent extends ReviewPaymentContainerComponent implements OnInit, OnDestroy {
  constructor(
    formBuilder: FormBuilder,
    stateService: StateService,
    @Inject(PUBSUB) eventBus: Pubsub,
    navigationService: NavigationService,
    createPaymentService: CreatePaymentService,
    @Optional() modalService: RoutableModalService,
    paymentOrderHttpService: PaymentOrderHttpService,
    createPaymentSetup: CreatePaymentConfigSetupService,
    @Optional() transactionSigningHelper: TransactionSigningHelperService,
    @Optional() @Inject(BB_PAYORD_PAYMENT_ERROR_MESSAGES_TOKEN) paymentErrors: PayordPaymentError,
  ) {
    super(
      formBuilder,
      stateService,
      eventBus,
      navigationService,
      createPaymentService,
      modalService,
      paymentOrderHttpService,
      createPaymentSetup,
      transactionSigningHelper,
      paymentErrors,
    );
  }

  ngOnInit(): void {
    super.ngOnInit();
  }

  ngOnDestroy(): void {
    super.ngOnDestroy();
  }
}
